<template>
  <div class="des" style="padding:5% 5%;">
   <h1>介绍</h1>
   <h2>ZVUI是什么</h2>
   <p style="font-size:16px">ZVUI是一套基于Vue开发的轻量级组件库,您可以从<a href="">npm</a>免费下载它</p>
   <h2>Note</h2>
   <p style="font-size:16px">为了避免您产生一些CSS的bug，建议您将以下样式添加到您的CSS中</p>
   <div class="code">
       <code><span class='code-entity'>*</span>{</code>  
        <code><span class='code-entity'>margin:</span> <span class='code-attr'>0;</span></code>      
        <code><span class='code-entity'>padding:</span> <span class='code-attr'>0;</span></code>     
        <code><span class='code-entity'>box-sizing:</span> <span class='code-attr'>border-box;</span></code>      
        <code>}</code>  
     </div>
  </div>
</template>

<script>

import ZIcon from '../components/ZIcon'
export default {
 created() {
     
 },
 data() {
     return {
        
     }
 },
  components:{
     ZIcon
  }
}

</script>
<style scoped lang="scss" >

     
</style>


