<!--  -->
<template>
  <div style="padding:0 5%;">
     <h1>Popover</h1>
     <div class="import-code"></div>
     <h3>基本用法</h3>
     <h5>position对应的有四个方向,分别是<span style="font-style:italic;">top</span> ,<span style="font-style:italic;">bottom</span>,<span style="font-style:italic;">left</span>,<span style="font-style:italic;">right</span></h5>
     <z-popover   position="top">
       <div slot="content">
         <p>Hello Top!</p>
       </div>
       <z-button type="danger">top</z-button>
     </z-popover>
     <z-popover position="bottom">
       <div slot="content">
         <p>Hello Bottom!</p>
       </div>
       <z-button type="info">bottom</z-button>
     </z-popover>
     <z-popover position="left">
       <div slot="content">
         <p>Hello Left!</p>
       </div>
       <z-button type="success">left</z-button>
     </z-popover>
     <z-popover position="right">
       <div slot="content">
         <p>Hello Right!</p>
       </div>
       <z-button type="warning">right</z-button>
     </z-popover>
     
     <div class="code">
         <code><<span class='code-entity'>z-popover</span> <span class='code-attr'>position</span>=<span class='code-tag'>"bottom"</span>></code>
         <code>&nbsp;<<span class='code-entity'>div</span> <span class='code-attr'>slot</span>=<span class='code-tag'>"content"</span>></code>      
         <code>&nbsp;&nbsp;<<span class='code-entity'>p</span>>Hello Top!</<span class='code-entity'>p</span>></code>                   
         <code>&nbsp;</<span class='code-entity'>div</span>></code>
         <code>&nbsp;<<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"primary"</span>>top</<span class='code-entity'>z-button</span>></code>                
         <code></<span class='code-entity'>z-popover</span>></code>
         <code><span class='code-entity'>//some code...</span></code>
     </div>
     
     <h3>鼠标移入</h3>
     <h5>trigger有两种状态分别是<span style="font-style:italic;">click</span> ,<span style="font-style:italic;">hover</span></h5>
     <z-popover trigger="hover"  position="top">
       <div slot="content">
         <p>Hello Top!</p>
       </div>
       <z-button type="danger">top</z-button>
     </z-popover>
     <z-popover trigger="hover" position="bottom">
       <div slot="content">
         <p>Hello Bottom!</p>
       </div>
       <z-button type="info">bottom</z-button>
     </z-popover>
     <z-popover trigger="hover" position="left">
       <div slot="content">
         <p>Hello Left!</p>
       </div>
       <z-button type="success">left</z-button>
     </z-popover>
     <z-popover trigger="hover" position="right">
       <div slot="content">
         <p>Hello Right!</p>
       </div>
       <z-button type="warning">right</z-button>
     </z-popover>
     <div class="code">
         <code><<span class='code-entity'>z-popover</span> <span class='code-attr'>trigger</span>=<span class='code-tag'>"hover"</span> <span class='code-attr'>position</span>=<span class='code-tag'>"bottom"</span>></code>
         <code>&nbsp;<<span class='code-entity'>div</span> <span class='code-attr'>slot</span>=<span class='code-tag'>"content"</span>></code>      
         <code>&nbsp;&nbsp;<<span class='code-entity'>p</span>>Hello Top!</<span class='code-entity'>p</span>></code>                   
         <code>&nbsp;</<span class='code-entity'>div</span>></code>
         <code>&nbsp;<<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"primary"</span>>top</<span class='code-entity'>z-button</span>></code>                
         <code></<span class='code-entity'>z-popover</span>></code>
         <code><span class='code-entity'>//some code...</span></code>
     </div>
     <h3>还可以这样使用</h3>
     <z-popover  position="right">
       <div slot="content">
         <z-button type="success">i'm here!</z-button>
       </div>
       <z-button type="warning">click me!</z-button>
     </z-popover>
     <div class="code">
         <code><<span class='code-entity'>z-popover</span>  <span class='code-attr'>position</span>=<span class='code-tag'>"bottom"</span>></code>
         <code>&nbsp;<<span class='code-entity'>div</span> <span class='code-attr'>slot</span>=<span class='code-tag'>"content"</span>></code>      
         <code>&nbsp;&nbsp;<<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"primary"</span>>i'm here!</<span class='code-entity'>z-button</span>></code>                   
         <code>&nbsp;</<span class='code-entity'>div</span>></code>
         <code>&nbsp;<<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"warning"</span>>click me!</<span class='code-entity'>z-button</span>></code>                
         <code></<span class='code-entity'>z-popover</span>></code>
         <code><span class='code-entity'>//some code...</span></code>
     </div>
     
     
     <h2>Properties</h2>
     <table>
       <thead>
         <tr>
           <th>Properties</th>
           <th>Description</th>
           <th>Type</th>
           <th>Default</th>
         </tr>
       </thead>
       <tbody>
         
         <tr>
           <td>trigger</td>
           <td>鼠标事件的分类</td>
           <td>String</td>
           <td>click</td>
         </tr>
         <tr>
           <td>position</td>
           <td>弹窗出现的位置</td>
           <td>String</td>
           <td>left</td>
         </tr>
         
       </tbody>
       
     </table>
     <div style="margin-top:20px;width=100%;border:1px solid lightgray"></div>
  </div>
</template>

<script>
import ZPopover from '../components/ZPopover'
import ZButton from '../components/ZButton'
export default {
  methods: {
    
  },
  components:{
    ZPopover,
    ZButton
  }
}

</script>
<style scoped lang="scss">
 
</style>


