<!--  -->
<template>
  <div style="padding:0 5%;">
     <h1>Progress</h1>
     <h3>基本用法</h3>
     <h6>percent属性可以控制进度条的宽度,取值范围(0-100)</h6>
     <h6><z-progress type='info' :percents='40'  ></z-progress></h6>  
     <h6><z-progress type='success' :percents='50'></z-progress></h6>
     <h6><z-progress type='primary' :percents='60'></z-progress></h6>
     <h6><z-progress type='warning' :percents='70'></z-progress></h6>
     <h6><z-progress type='danger' :percents='80'></z-progress></h6>
       
     <div class="code">
        <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"info"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>40</span>></<span class='code-entity'>z-progress</span>></code>
        <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"success"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>50</span>></<span class='code-entity'>z-progress</span>></code>
        <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"primary"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>60</span>></<span class='code-entity'>z-progress</span>></code>
        <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"warning"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>70</span>></<span class='code-entity'>z-progress</span>></code>
        <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"danger"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>80</span>></<span class='code-entity'>z-progress</span>></code>           
     </div>
     
     <h3>带有数字的proggress</h3>
     <h6>progress属性可以选择是否显示数字</h6>
     <h6><z-progress type='info' :percents='40' :progress="true"  ></z-progress></h6>  
     <h6><z-progress type='success' :percents='50' :progress="true" ></z-progress></h6>
     <h6><z-progress type='primary' :percents='60' :progress="true" ></z-progress></h6>
     <h6><z-progress type='warning' :percents='70' :progress="true" ></z-progress></h6>
     <h6><z-progress type='danger' :percents='80' :progress="true" ></z-progress></h6>
     <div class="code">
       <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"info"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>40</span>  <span class='code-attr'>progress</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-progress</span>></code>
       <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"success"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>50</span>  <span class='code-attr'>progress</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-progress</span>></code>
       <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"primary"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>60</span>  <span class='code-attr'>progress</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-progress</span>></code>
       <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"warning"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>70</span>  <span class='code-attr'>progress</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-progress</span>></code>
       <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"danger"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>80</span>  <span class='code-attr'>progress</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-progress</span>></code>     
     </div>
     <h3>带有动画的progress</h3>
     <h6><z-progress  type='danger' :percents='percent' :progress="true" ref="z-progress"></z-progress></h6>
     <span><z-button @click="add1" type="primary">+</z-button></span>
     <span><z-button @click="sub1" type="success">-</z-button></span>
     <div class="code"> 
       <code><<span class='code-entity'>z-progress</span> <span class='code-attr'>type</span>=<span class='code-tag'>"info"</span> <span class='code-attr'>:percents</span>=<span class='code-tag'>"percent"</span>  <span class='code-attr'>progress</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-progress</span>></code>    
       <code><<span class='code-entity'>div</span> <span class='code-attr'>@click</span>=<span class='code-tag'>"percent+=10"</span>>+</<span class='code-entity'>div</span>></code>    
       <code><<span class='code-entity'>div</span> <span class='code-attr'>@click</span>=<span class='code-tag'>"percent-=10"</span>>-</<span class='code-entity'>div</span>></code>    
     </div>
     <h2>Properties</h2>
     <table>
       <thead>
         <tr>
           <th>Properties</th>
           <th>Description</th>
           <th>Type</th>
           <th>Default</th>
         </tr>
       </thead>
       <tbody>
         
         <tr>
           <td>type</td>
           <td>样式名称</td>
           <td>String</td>
           <td>primary</td>
         </tr>
         <tr>
           <td>percents</td>
           <td>进度条的宽度</td>
           <td>Number</td>
           <td>0</td>
         </tr>
         <tr>
           <td>progress</td>
           <td>是否显示进度的数字</td>
           <td>Boolean</td>
           <td>false</td>
         </tr>
       </tbody>
       
     </table>
    <div style="margin-top:20px;width=100%;border:1px solid lightgray"></div>  
  </div>
</template>

<script>
import ZProgress from '../components/ZProgress'
import ZButton from '../components/ZButton'
export default {
  data() {
    return {
      percent:0
    }
  },
  methods: {
    add1(){ 
     if(this.percent>=100)return
     this.percent+=10; 
    
    },
    sub1(){
      if(this.percent<=0)return
      this.percent-=10; 
    }
  },
  components:{
  ZProgress,ZButton
  }
}

</script>
<style scoped lang="scss">
 
</style>


