<!--  -->
<template>
  <div style="padding:0 5%;">
     <h1>Button</h1>  
     <div class="import-code"></div>
     <h3 >基本用法</h3>
     <z-button  type="info">info</z-button>
     <z-button type="warning">warning</z-button>
     <z-button type="danger">danger</z-button>
     <z-button type="success">success</z-button>
     <z-button type="primary">primary</z-button>
     
     <div class="code">
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"info"</span>>info</<span class='code-entity'>z-button</span>></code>
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"warning"</span>>warning</<span class='code-entity'>z-button</span>></code>      
         <code> <<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"danger"</span>>danger</<span class='code-entity'>z-button</span>> </code>     
         <code> <<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"success"</span>>success</<span class='code-entity'>z-button</span>> </code>     
         <code> <<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"primary"</span>>primary</<span class='code-entity'>z-button</span>>  </code>                
     </div>
     
     <h3>透明的Button</h3>
     <p>在type后面加上transparent</p>
     <z-button  type="info-transparent">info</z-button>
     <z-button type="warning-transparent">warning</z-button>
     <z-button type="danger-transparent">danger</z-button>
     <z-button type="success-transparent">success</z-button>
     <z-button type="primary-transparent">primary</z-button>
     <div class="code">
       <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"info-transparent"</span>>info</<span class='code-entity'>z-button</span>></code>  
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"warning-transparent"</span>>warning</<span class='code-entity'>z-button</span>></code>      
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"danger-transparent"</span>>danger</<span class='code-entity'>z-button</span>> </code>     
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"success-transparent"</span>>success</<span class='code-entity'>z-button</span>></code>      
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>"primary-transparent"</span>>primary</<span class='code-entity'>z-button</span>></code>        
     </div>
     
     <h3>带有Loading动画的Button</h3>
     <p>设置icon-name,isLoading为true or false</p>
     <z-button type="info" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="warning" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="danger" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="success" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="primary" icon-name="loading" :isLoading="true">loading</z-button>
     <div class="code">
       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"info"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>      
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"warning"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>      
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"danger"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>      
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"success"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>      
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"primary"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>      
       </code>
     </div>
     
     <h3>透明Loading动画的Button</h3>
     
     <z-button type="info-transparent" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="warning-transparent" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="danger-transparent" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="success-transparent" icon-name="loading" :isLoading="true">loading</z-button>
     <z-button type="primary-transparent" icon-name="loading" :isLoading="true">loading</z-button>
     <div class="code">
       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"info-transparent"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>      
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"warning-transparent"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>     
              
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"danger-transparent"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>        
              
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"success-transparent"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>   
                   
       </code>

       <code><<span class='code-entity'>z-button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>"warning-transparent"</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>"loading"</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>"true"</span><span>>loading</</span><span class='code-entity'>z-button</span>>       
              
       </code>        
     </div>
     <h2>Properties</h2>
     <table>
       <thead>
         <tr>
           <th>Properties</th>
           <th>Description</th>
           <th>Type</th>
           <th>Default</th>
         </tr>
       </thead>
       <tbody>
         
         <tr>
           <td>type</td>
           <td>样式名称</td>
           <td>String</td>
           <td>primary</td>
         </tr>
         <tr>
           <td>icon-name</td>
           <td>icon名称</td>
           <td>String</td>
           <td>''</td>
         </tr>
         <tr>
           <td>isLoading</td>
           <td>是否开启动画</td>
           <td>Boolean</td>
           <td>false</td>
         </tr>
         <tr>
           <td>type-transparent</td>
           <td>是否选择透明的样式</td>
           <td>String</td>
           <td>''</td>
         </tr>
       </tbody>
       
     </table>
     <div style="margin-top:20px;width=100%;border:1px solid lightgray"></div>
  </div>
</template>

<script>
import ZButton from '../components/ZButton'
export default {
  components:{
    ZButton
  },
  methods: {
   
  },
}

</script>
<style scoped lang="scss">
 
</style>


