import Vue from 'vue'
import Router from 'vue-router'
import docButton from '@/docs/docbutton'
import docPopover from '@/docs/docpopover'
import docIntroduction from '@/docs/docIntroduction'
import docInstall from '@/docs/docInstall'
import docProgress from '@/docs/docProgress'
import docSwitch from '@/docs/docSwitch'
import docSettings from '@/docs/docSettings'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      component:docIntroduction
    },
    {
      path:'/Install',
      component:docInstall
    },
    {
      path: '/components/button',
      component: docButton
    },
    {
      path: '/components/popover',
      component: docPopover
    },
    {
      path: '/components/progress',
      component: docProgress
    },
    {
      path: '/components/switch',
      component: docSwitch
    },
    {
      path: '/components/settings',
      component: docSettings
    }
   

    
  ]
})



