<!--  -->
<template>
  <div style="padding:0 5%;">
     <h1>Setting</h1>
     
     <div class="import-code"></div>
     <h3>基本用法</h3>
     <h6>options接受的是一个数组类型的数据</h6>
     <h6 style="display:flex"><span style="font-size:30;line-height:30px;margin-right:10px;">显示设置</span> <z-setting :options="['1920X1080','1600X900','1440X900','1024X768']"></z-setting></h6>  
     <h6 style="display:flex"><span style="font-size:30;line-height:30px;margin-right:10px;">垂直同步</span> <z-setting :options="['开启','关闭']"></z-setting></h6>  
    
       
     <div class="code">
        <code><<span class='code-entity'>z-setting</span> <span class='code-attr'>:options</span>=<span class='code-tag'>"['1920X1080','1600X900','1440X900','1024X768']"</span>></<span class='code-entity'>z-setting</span>></code>
        <code><<span class='code-entity'>z-setting</span> <span class='code-attr'>:options</span>=<span class='code-tag'>"['开启','关闭']"</span>></<span class='code-entity'>z-setting</span>></code>                  
     </div>
     
     <h3>带有过渡动画的setting</h3>
     <h6>ani接受的是一个boolean的数据</h6>
     <h6 style="display:flex"><span style="font-size:30;line-height:30px;margin-right:10px;">显示设置</span> <z-setting :options="['1920X1080','1600X900','1440X900','1024X768']" :ani="true"></z-setting></h6>  
     <h6 style="display:flex"><span style="font-size:30;line-height:30px;margin-right:10px;">垂直同步</span> <z-setting :options="['开启','关闭']" :ani="true"></z-setting></h6> 
     <div class="code">
       <code><<span class='code-entity'>z-setting</span> <span class='code-attr'>:options</span>=<span class='code-tag'>"['1920X1080','1600X900','1440X900','1024X768']"</span> <span class='code-attr'>:ani</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-setting</span>></code>
        <code><<span class='code-entity'>z-setting</span> <span class='code-attr'>:options</span>=<span class='code-tag'>"['开启','关闭']"</span> <span class='code-attr'>:ani</span>=<span class='code-entity'>"true"</span>></<span class='code-entity'>z-setting</span>></code>        
     </div>
     
     
     <h2>Properties</h2>
     <table>
       <thead>
         <tr>
           <th>Properties</th>
           <th>Description</th>
           <th>Type</th>
           <th>Default</th>
         </tr>
       </thead>
       <tbody>
         
         <tr>
           <td>options</td>
           <td>要显示的数据</td>
           <td>Array</td>
           <td>[]</td>
         </tr>
         <tr>
           <td>ani</td>
           <td>是否开启动画</td>
           <td>Boolean</td>
           <td>false</td>
         </tr>
         <tr>
           <td>isLoading</td>
           <td>type name</td>
           <td>Boolean</td>
           <td>false</td>
         </tr>
         <tr>
           <td>type-transparent</td>
           <td>description</td>
           <td>String</td>
           <td>''</td>
         </tr>
       </tbody>
       
     </table>
    <div style="margin-top:20px;width=100%;border:1px solid lightgray"></div> 
  </div>
</template>

<script>
import ZSetting from '../components/Zsetting'
export default {
  data() {
    return {
      percent:0
    }
  },
  methods: {
    add1(){ 
     if(this.percent>=100)return
     this.percent+=10; 
    
    },
    sub1(){
      if(this.percent<=0)return
      this.percent-=10; 
    }
  },
  components:{
   ZSetting
  }
}

</script>
<style scoped lang="scss">
 
</style>


