<!--  -->
<template>
  <div class="des" style="padding:5% 5%;">
      <h1>安装</h1>
      <pre style="background:#1e1e1e;height:20px;padding:3px 0;">
        <code><span style="color:white">npm install ZVUI --save</span></code>
      </pre>
      <h1>导入</h1>
      <div class="code">
       <code><span class='code-entity'>import</span>   <span class='code-attr'>{ZButton}</span>   <span class='code-tag'>from</span>   <span class='code-entity'>"zvui"</span></code>  
       <code><span class='code-entity'>import</span>   <span class='code-attr'></span><span class='code-tag'>from</span>   <span class='code-entity'>"zvui-css"</span></code>      
      </div>
      <h1>如何使用</h1>
      <div class="code">
       <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>info-transparent</span>>info</<span class='code-entity'>z-button</span>></code>  
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>warning-transparent</span>>warning</<span class='code-entity'>z-button</span>></code>      
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>danger-transparent</span>>danger</<span class='code-entity'>z-button</span>> </code>     
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>success-transparent</span>>success</<span class='code-entity'>z-button</span>></code>      
        <code><<span class='code-entity'>z-button</span> <span class='code-attr'>type</span>=<span class='code-tag'>primary-transparent</span>>primary</<span class='code-entity'>z-button</span>></code>        
      </div>
      <h1>看！</h1>
      <z-button  type="info-transparent">info</z-button>
      <z-button type="warning-transparent">warning</z-button>
      <z-button type="danger-transparent">danger</z-button>
      <z-button type="success-transparent">success</z-button>
      <z-button type="primary-transparent">primary</z-button>

  </div>
  

  
</template>

<script>


import ZButton from '../components/ZButton'
export default {
 created() {
     
 },
 data() {
     return {
        
     }
 },
  components:{
     ZButton
  }
}

</script>
<style scoped lang="scss" >

     
</style>


